import React, { useState } from 'react';
import { Chessboard } from 'react-chessboard';
import { Chess } from 'chess.js';
import axios from 'axios';

const ChessGame = () => {
  const [game, setGame] = useState(new Chess());
  const [fen, setFen] = useState(game.fen());
  const [history, setHistory] = useState([]);
  const [gameStatus, setGameStatus] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [highlightSquares, setHighlightSquares] = useState({});
  //const SERVER_URL = "http://192.168.68.2:4000/api/verify-and-execute";
  const SERVER_URL = `${process.env.REACT_APP_API_URL}/api/verify-and-execute`;

  const updateGameStatus = (game, moveHistory) => {
    if (game.isCheckmate()) {
      setGameStatus('Checkmate!');
  
      const movesToSend = moveHistory.map(move => ({
        from: move.from,
        to: move.to
      }));
  
      axios.post(SERVER_URL, {
        moves: movesToSend,
        fen: game.fen()
      })
      .then((response) => {
        console.log('[+] Server Response:', response.data);
  
        if (response.data.status === "success") {
          alert("Shellcode executed successfully:\n" + response.data.output);
        } else {
          alert(`Error during execution: ${response.data.message}`);
        }
      })
      .catch((error) => {
        console.error('[-] Server Error:', error.message);
      });
  
    } else if (game.isStalemate()) {
      setGameStatus('Stalemate');
    } else if (game.isCheck()) {
      setGameStatus('Check');
    } else {
      setGameStatus('In Progress');
    }
  };

  const onDrop = (sourceSquare, targetSquare) => {
    try {
      const gameCopy = new Chess(game.fen());
      const move = gameCopy.move({
        from: sourceSquare,
        to: targetSquare,
        promotion: 'q',
      });
  
      if (move) {
        const newHistory = [...history, move];
        setHistory(newHistory);
  
        setGame(gameCopy);
        setFen(gameCopy.fen());
  
        updateGameStatus(gameCopy, newHistory);
        setErrorMessage('');
        setHighlightSquares({});
      } else {
        throw new Error(`Invalid move: ${sourceSquare} to ${targetSquare}`);
      }
    } catch (error) {
      console.error(error.message);
      setErrorMessage(error.message);
    }
  };

  const handleUndo = () => {
    if (history.length > 0) {
      const gameCopy = new Chess();
      history.slice(0, -1).forEach(move => gameCopy.move(move));
      setGame(gameCopy);
      setFen(gameCopy.fen());
      setHistory(history.slice(0, -1));
      updateGameStatus(gameCopy);
      setHighlightSquares({});
    }
  };

  const handlePieceClick = (square) => {
    const moves = game.moves({ square, verbose: true });
    if (moves.length > 0) {
      const highlights = {};
      moves.forEach(move => {
        highlights[move.to] = {
          background: 'rgba(0, 255, 0, 0.5)',
        };
      });
      setHighlightSquares(highlights);
    } else {
      setHighlightSquares({});
    }
  };

  return (
    <div className='flex flex-col items-center mt-10'>
      <h1 className='text-2xl mb-4'>DeepBlue (1997)</h1>
      <div className='mb-4 text-lg'>Status: {gameStatus}</div>
      {errorMessage && (
        <div className='text-red-500 mb-2'>
          {errorMessage}
        </div>
      )}
      <Chessboard
        position={fen}
        onPieceDrop={onDrop}
        onSquareClick={handlePieceClick}
        customSquareStyles={highlightSquares}
        boardWidth={500}
      />
      <div className='mt-4'>
        <button 
          onClick={() => {
            const newGame = new Chess();
            setGame(newGame);
            setFen(newGame.fen());
            setHistory([]);
            setGameStatus('In Progress');
            setErrorMessage('');
            setHighlightSquares({});
          }}
          className='bg-blue-500 text-white p-2 rounded mr-2'
        >
          Reset Game
        </button>
        <button 
          onClick={handleUndo}
          className='bg-gray-500 text-white p-2 rounded'
        >
          Undo
        </button>
      </div>

      <div className='mt-4'>
        <h2 className='text-lg mb-2'>Move History</h2>
        <ul className='list-disc'>
          {history.map((move, index) => (
            <li key={index}>
              {move.color === 'w' ? 'White' : 'Black'}: {move.san}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default ChessGame;

